body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background: linear-gradient(171.09deg, #646FD4 17.77%, rgba(100, 111, 212, 0.812201) 25.99%, rgba(100, 111, 212, 0) 86.34%); */
  background: linear-gradient(171.09deg, #64c9d4 17.77%, rgba(100, 212, 181 ,0.81) 25.99%, rgba(100, 111, 212, 0) 86.34%);

  background-repeat: no-repeat;
  min-width: 320px;
  height: 95vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

