body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background: linear-gradient(171.09deg, #646FD4 17.77%, rgba(100, 111, 212, 0.812201) 25.99%, rgba(100, 111, 212, 0) 86.34%); */
  background: linear-gradient(171.09deg, #64c9d4 17.77%, rgba(100, 212, 181 ,0.81) 25.99%, rgba(100, 111, 212, 0) 86.34%);

  background-repeat: no-repeat;
  min-width: 320px;
  height: 95vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.TodoButtomMain{
position: fixed;
display: flex;
justify-content: center;
z-index: 1;
bottom: 3vh;
    right: 3vh;

}

.TodoButtom{
    width: 56px;
    height: 56px;
    /* background: #646fd4; */
    background: #64c9d4;
    box-shadow: 1px 1px 4px rgba(0,0, 0, 0.25);
    border-radius: 56px;
    font-size: 30px;
    color: white;
    cursor: pointer;
    
}
.TodoCounter-Title{
    text-align: center;
}
.TodoItem{
    position: relative;
    scroll-snap-align: end;
    width: 100%;
    margin-top: 5px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    border-radius: 15px;
    border: 1px solid #dcdce5;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    background-color: white;
}

.TodoItem-p{
    margin: 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
}

.TodoItem-p--completed{
    text-decoration: line-through;
}

.Icon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
}

.Icon-check--active{
    color: #4caf50;
}

.Icon-delete:hover {
    color: red;
}
/*  */
.TodoListMain{
    display: flex;
    justify-content: center;
    height: 70vh;
    width: 100%;

}
.TodoList{
    display: flex;
    flex-direction: column;
    padding-left: 0px;
    overflow-y: scroll;
    width: 95%;
    

}
.TodoSearchMain{
    display: flex;
    flex-direction: row;
    align-items: center;
    margin: 0 auto;
    width: 80%;
    height: 45px;
    background-color: white;
    border-radius: 15px;
    border: 1px solid #dcdce5;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);

}

.TodoSearchInput{
    width: 80%;
    height: 85%;
    background-color: transparent;
    border: none;

    

}
.ModalBackground {
    background: rgba(32,35,41,.8);
    position: fixed;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
  }
